import React from 'react';
import Bundle from '../projectTools/bundle';
export default [
  {
    path: '/sale_search',
    exact: true,
    component(props) {
      let breadcrumbList = [
        {
          name: '销售查询',
          path: ''
        }
      ]
      let title = '销售查询'
      return <Bundle {...props} load={() => import('../components/SaleSearch')} breadcrumbList={breadcrumbList} title={title}/>;
    }
  },
  {
    path: '/sale_search/:goodsNo',
    exact: true,
    component(props) {
      let breadcrumbList = [
        {
          name: '销售查询',
          path: '/sale_search'
        },
        {
          name: '商品详情',
          path: ''
        }
      ]
      let title = '门店中商品详情'
      return <Bundle {...props} load={() => import('../components/SaleSearch/GoodsDetail')} breadcrumbList={breadcrumbList} title={title}/>;
    }
  }
]