import React, { Component } from 'react';
import { Row, Col, Button, Icon, Table } from 'antd';
import BarCode from '../Common/BarCode';
import './style.less';
import Storage from '../../utils/localStorage';
import printElement from '../Common/WinPrint';
import orderMgtUrl from '../../api/orderMgtUrl';
import utils from '../../utils/utils';

class SearchOrder extends Component {
  // 初始化页面常量 绑定事件方法
  constructor(props) {
    super(props)
    this.state = {
      paperNo: props.match.params.id || '',
      orderMsg: Storage.get("orderMsg"),
      pageNum: 1,
      pageSize: 2000,
      columns: [
        {
          title: '货号',
          dataIndex: 'vendorGoodsNo',
          width: 60
        },
        {
          title: '条码',
          dataIndex: 'barcode',
          width: 70
        },
        {
          title: '商品编码',
          dataIndex: 'erpGoodsNo',
          width: 80
        },
        {
          title: '商品名称',
          dataIndex: 'goodsName',
          width: 90
        },
        {
          title: '单位',
          dataIndex: 'unit',
          width: 70
        },
        {
          title: '规格',
          dataIndex: 'spec',
          width: 70
        },
        {
          title: '订单金额',
          dataIndex: 'amount',
          width: 80
        },
        {
          title: '订单数量',
          dataIndex: 'qty',
          width: 80
        },
        {
          title: '收货数量',
          dataIndex: 'receiveQty',
          width: 80
        },
        {
          title: '赠品数量',
          dataIndex: 'giftQty',
          width: 80
        },
        {
          title: '收货单价',
          dataIndex: 'price',
          width: 80
        },
        {
          title: '收货总金额',
          dataIndex: 'totalReceiveAmount',
          width: 100
        },
        {
          title: '备注',
          dataIndex: 'remark',
          width: 90
        }
      ],
      tableData: []
    }
  }

  componentDidMount() {
    this.getDetailHandel()
  }

  render() {
    let {paperNo, storeName, createUserName, createUserContact, authDatetime, expectDatetime} = this.state.orderMsg;
    let tableData = this.state.tableData;
    let columns = this.state.columns;
    return (
      <div className="common-main-container">
        <div className="left-container-wrap">
          <Button type="primary" onClick={this.doPrintHandel}><Icon type="printer" />打印单据</Button>
        </div>
        <div className="right-container-wrap print-content" ref={(el) => {this.refs=el}}>
          <div className="print-header operation-wraper">
            <h3 style={{textAlign: 'center', fontWeight: 700, fontSize: 18}}>订货管理单</h3>
            <Row type="flex" align="bottom" gutter={10}>
              <Col span={16}>
                <Row>
                  <Col span={12}>
                    <span style={{paddingLeft: '10px'}}>门店/仓库：{storeName || '--'}</span>
                  </Col>
                  <Col span={12}>
                    <span style={{paddingLeft: '10px'}}>订单编号：{paperNo}</span>
                  </Col>
                </Row>
                <Row>
                  <Col span={12}>
                    <span style={{paddingLeft: '10px'}}>联系人: {createUserName || '--'}</span>
                  </Col>
                  <Col span={12}>
                    <span style={{paddingLeft: '10px'}}>联系电话: {createUserContact || '--'}</span>
                  </Col>
                </Row>
                <Row>
                  <Col span={12}><span style={{paddingLeft: '10px'}}>单据时间: {authDatetime || '--'}</span></Col>
                  <Col span={12}><span style={{paddingLeft: '10px'}}>期望送达时间: {expectDatetime || '--'}</span></Col>
                </Row>
              </Col>
              <Col span={8}>
                <BarCode paperNo={paperNo}/>
              </Col>
            </Row>
          </div>
          <div className="print-table content-wraper"> 
            <Table 
              columns={columns} 
              dataSource={tableData} 
              loading={this.state.loading}
              size="middle" 
              pagination={false}/>
          </div>
        </div>
      </div>
    );
  }

  getDetailHandel = () => {
    this.setState({
      loading: true
    })
    let {pageNum, pageSize, paperNo} = this.state
    let _params = {pageNum,pageSize,paperNo}
    this.$http.post(orderMgtUrl.orderDetail, {..._params}).then(res => {
      this.setState({
        loading: false
      })
      if(res.code === 0) {
        let tableDataList = res.data && res.data.result && res.data.result.length ? res.data.result : []
         let tableData = tableDataList.map(item => {
          item.unique = `${item.barcode}${item.erpGoodsNo}${item.vendorGoodsNo}`
          return item 
         })
        this.setState({
          tableData: tableData
        })
        return
      }
      throw new Error(res.msg)
    }).catch(e => {
      this.setState({
        loading: false
      })
      utils.openNotification('error', '系统提示', e.message || '')
    })
  }

  doPrintHandel = () => {
    printElement({
      content: this.refs,
    }).then(() => {});
  }
}

export default SearchOrder;
