import React from 'react';
import Bundle from '../projectTools/bundle';
export default [
  {
    path: '/order_mgt',
    exact: true,
    component(props) {
      let breadcrumbList = [
        {
          name: '订货管理',
          path: ''
        }
      ]
      let title = '订货管理'
      return <Bundle {...props} load={() => import('../components/OrderMgt')} breadcrumbList={breadcrumbList} title={title}/>;
    }
  },
  {
    path: '/order_mgt/:id',
    exact: true,
    component(props) {
      let breadcrumbList = [
        {
          name: '订货管理',
          path: '/order_mgt'
        },
        {
          name: '订单详情',
          path: ''
        }
      ]
      let title = '订单详情'
      return <Bundle {...props} load={() => import('../components/OrderMgt/OrderDetail')} breadcrumbList={breadcrumbList} title={title}/>;
    }
  }
]